b {
	color: #006400;
}

table {
    border-collapse: collapse;
    width: 50%;
}

th, td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

.inline_code{
	font-weight: bold;
	color: #181d46;
	display: inline;
}

tr:hover {background-color:#f5f5f5;}

.App {
  text-align: center;
}

.App-logo {
  height: 80px;
}

.App-logo:hover {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.SearchBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.SearchBar input[type="text"] {
  display: block;
  height: 40px;
  margin: 0;
  border: solid #449cfa 1.5px;
  width: 99%;
  font-family: sans-serif;
  padding-left: 1%;
  font-size: 18px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  border-radius: 5px 0px 0px 5px;
}

input[type="submit"] {
  margin-left: 0px;
  font-size: 14px;
  height: 45px;
  width: 125px;
  border-radius: 0 5px 5px 0;
  background: #449cfa;
  color: white;
  border: 0;
  -webkit-appearance: none;
  cursor: pointer;
  outline: none;
}

.MyButton {
	background-color: white; /* Green */
	border: none;
	color: black;
	padding: 16px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	margin: 4px 2px;
	-webkit-transition-duration: 0.4s; /* Safari */
	-o-transition-duration: 0.4s;
	   transition-duration: 0.4s;
	cursor: pointer;
	border: 2px solid #4CAF50;
}

.MyButton:hover {
	background-color: #4CAF50;
	color: white;
}


